'use client';
/* feature_cards_4-pagePart.tsx */

/* Import custom components */
import Heading1PagePart from '@/components/heading/heading_1-pagePart/heading_1-pagePart';

/* Import styles */
import styles from './poster_cards_3-pagePart.module.scss';

/* Define the data type of each card */
interface FeatureCardData {
  heading: string;
  description: string; // Card description text
}

interface Props {
  cardInfoP?: FeatureCardData[]; // Array of feature card data
}

/**
 * This component renders a set of feature cards with headings and descriptions.
 * A default set of cards is used if no props are provided.
 *
 * @param cardInfoP - Optional array of feature cards.
 *
 * @example
 * ```tsx
 * <PosterCards3PagePart
 *   cardInfoP=[
 *     {
 *       heading: 'Security Monitoring',
 *       description: 'Real-time security assessments and alerting.',
 *     }
 *   ]
 * />
 * ```
 */
const PosterCards3PagePart: React.FC<Props> = ({ cardInfoP }) => {
  // Default card data (used if no props are provided)
  const defaultCardInfo: FeatureCardData[] = [
    {
      heading: 'Monitor your entire attack surface',
      description:
        'Continuous security checks as your team deploys. Automated workflows to enable instant, organization-wide detection and triage. Transform noisy, ineffective scan results into relevant and actionable alerts.',
    },
    {
      heading: 'Monitor your entire attack surface',
      description:
        'Continuous security checks as your team deploys. Automated workflows to enable instant, organization-wide detection and triage. Transform noisy, ineffective scan results into relevant and actionable alerts.',
    },
    {
      heading: 'Monitor your entire attack surface',
      description:
        'Continuous security checks as your team deploys. Automated workflows to enable instant, organization-wide detection and triage. Transform noisy, ineffective scan results into relevant and actionable alerts.',
    },
    {
      heading: 'Monitor your entire attack surface',
      description:
        'Continuous security checks as your team deploys. Automated workflows to enable instant, organization-wide detection and triage. Transform noisy, ineffective scan results into relevant and actionable alerts.',
    },
  ];

  // Use provided card data if available, otherwise fallback to default
  const cardInfo = cardInfoP || defaultCardInfo;

  return (
    <div className={`${styles.poster_cards_3_pagePart}`}>
      <Heading1PagePart />
      <div className={styles.cards}>
        {/* dynamically apply array items */}
        {cardInfo.map((item, index) => {
          return (
            <div key={index} className={`${styles.card} card border_type_1`}>
              {/* Card content */}
              <h3 className={styles.heading}>{item.heading}</h3>
              <p className={styles.description}>{item.description}</p>
            </div>
          );
        })}
      </div>
    </div>
  );
};

PosterCards3PagePart.displayName = 'PosterCards3PagePart';

export default PosterCards3PagePart;
