'use client';
/* feature_cards_4-pagePart.tsx */
import { ReactElement } from 'react';

/* Import medias */
import { Cpu } from 'lucide-react';

/* Import custom components */
import Heading1PagePart from '@/components/heading/heading_1-pagePart/heading_1-pagePart';

/* Import styles */
import styles from './feature_cards_4-pagePart.module.scss';

/* Define the data type of each card */
interface FeatureCardData {
  icon: ReactElement; // React component for the icon
  iconColor?: string; // Hex color for the icon glow (optional, default to teal)
  tag: string;
  heading: string;
  description: string; // Card description text
}

interface Props {
  cardInfoP?: FeatureCardData[]; // Array of feature card data
}

/**
 * This component renders a set of feature cards with dynamic icons, colors, and descriptions.
 * A default set of cards is used if no props are provided.
 * If no `iconColor` is passed, default color will be applied
 *
 * @param cardInfoP - Optional array of feature cards.
 *
 * @example
 * ```tsx
 * <FeatureCards4PagePart
 *   cardInfoP={[
 *     {
 *       icon: <Cpu color="#32cd32" />, // Bright green
 *       iconColor: '#32cd32', (this is optional. If no color passed then default color will be applied. see code to change the default value)
 *       tag: 'Performance',
 *       heading: 'Asset Utilization',
 *       description: 'You have 600 assets in the last 30 days',
 *     }
 *   ]}
 * />
 * ```
 */

const FeatureCards4PagePart: React.FC<Props> = ({ cardInfoP }) => {
  // Default card data (used if no props are provided)
  const defaultCardInfo: FeatureCardData[] = [
    {
      icon: <Cpu color="#32cd32" />, // Bright green
      iconColor: '#32cd32',
      tag: 'Performance',
      heading: 'Asset Utilization',
      description: 'You have 600 assets in the last 30 days',
    },
    {
      icon: <Cpu color="#1e90ff" />, // Bright blue
      iconColor: '#1e90ff',
      tag: 'Connectivity',
      heading: 'Network Speed',
      description: 'Network speeds have improved by 40%',
    },
    {
      icon: <Cpu color="#ff1493" />, // Bright pink
      iconColor: '#ff1493',
      tag: 'Security',
      heading: 'Protocol Updates',
      description: 'Security protocols updated to the latest standards',
    },
    {
      icon: <Cpu color="#ffda00" />, // Bright yellow
      iconColor: '#ffda00',
      tag: 'Resource Management',
      heading: 'Optimal Allocation',
      description: 'Allocated resources are at optimal levels',
    },
  ];

  // Use provided card data if available, otherwise fallback to default
  const cardInfo = cardInfoP || defaultCardInfo;

  // Add default value here
  const defaultIconColor = '#30d6ad';

  return (
    <div className={`${styles.feature_cards_1_pagePart}`}>
      <Heading1PagePart />
      <div className={styles.cards}>
        {/* dynamically apply array items */}
        {cardInfo.map((item, index) => {
          const iconColor = item.iconColor || defaultIconColor;

          return (
            <div key={index} className={`${styles.card} card border_type_1`}>
              {/* Neon glow effect positioned at the top-left of the card */}
              <div
                style={{
                  position: 'absolute',
                  top: '-42px',
                  left: '-42px',
                  width: '50px',
                  height: '50px',
                  backgroundColor: iconColor,
                  borderRadius: '50%',
                  boxShadow: `
                    0px 0px 50px 40px ${iconColor}66,
                    0px 0px 100px 60px ${iconColor}4C,
                    0px 0px 300px 80px ${iconColor}33,
                    0px 0px 900px 150px ${iconColor}1A
                  `,
                  zIndex: '-1',
                  pointerEvents: 'none', // Prevents interaction issues
                  filter: 'blur(15px)',
                }}
              ></div>

              {/* Card content */}
              <div className={`${styles.icon} icon_cont_type_1`}>
                {item.icon}
              </div>
              <div className={styles.text_cont}>
                <div className={styles.tag}>
                  FEATURE - VULNERABILITY MANAGEMENT
                </div>
                <h3 className={styles.heading}>{item.heading}</h3>
                <p className={styles.description}>{item.description}</p>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
};

FeatureCards4PagePart.displayName = 'FeatureCards4PagePart';

export default FeatureCards4PagePart;
