'use client';
import { JSX } from 'react';

/* Import media */
import { Circle } from 'lucide-react';

/* Import styles */
import styles from './tags_1-pagePart.module.scss';

/* Data structure for individual tag items. */
interface Tags1PagePartDataTypes {
  name: string;
  value: string;
  icon?: JSX.Element;
}

interface Props {
  tagsP?: Tags1PagePartDataTypes[];
}

/**
 * A component that renders a list of tags.
 *
 * @example
 * ```tsx
 * <Tags1PagePart
 *   tagsP={[
 *     { name: "Google Cloud", value: "google_cloud", icon: Circle, iconColor: "blue" },
 *     { name: "Leadership", value: "leadership", icon: Circle, iconColor: "red" },
 *   ]}
 * />
 * ```
 */

const Tags1PagePart: React.FC<Props> = ({ tagsP }) => {
  // Default tag items if none are provided
  const defaultTags: Tags1PagePartDataTypes[] = [
    { name: 'Pending', value: 'pending', icon: <Circle stroke="coral" /> },
  ];

  // Fallback to defaultTags if no tagsP passed as prop
  const tags = tagsP || defaultTags;

  return (
    // Container for tags
    <div className={styles.tags_1_pagePart}>
      {tags.map((item, index) => {
        return (
          // Individual tag element
          <div key={index} className={styles.tag} data-value={item.value}>
            <div className={`${styles.icon} icon_cont_type_1`}>
              {item.icon ? item.icon : <Circle stroke="red" />}
            </div>
            <div className={styles.name}>{item.name}</div>
          </div>
        );
      })}
    </div>
  );
};

// Set displayName for easier debugging
Tags1PagePart.displayName = 'Tags1PagePart';

export default Tags1PagePart;
