'use client';
/* quote_1-pagePart.module.tsx */
import React, { useState, useEffect } from 'react';

/* Import media */
import { Quote } from 'lucide-react';

/* Import styles */
import styles from './quote_1-pagePart.module.scss';

/* Interface representing the quote information. */
interface Quote1PagePartDataTypes {
  icon?: React.ReactNode;
  list: {
    text: string;
    author: string;
  }[];
}

interface Quote1PagePartProps {
  infoP?: Quote1PagePartDataTypes;
  intervalTime?: number; // Time in ms for auto-switching quotes
}

const Quote1PagePart: React.FC<Quote1PagePartProps> = ({
  infoP,
  intervalTime = 6000,
}) => {
  const [activeIndex, setActiveIndex] = useState<number>(0);

  // Default quote information if no info is provided via props
  const defaultInfo: Quote1PagePartDataTypes = {
    icon: <Quote color="var(--primary-color, teal)" />, // Default icon
    list: [
      {
        text: "We don't even have to wait for a scan to finish before we can start patching — we can do it straight away and then instantly see our risk score go down. This is incredibly motivating to our team.",
        author: '--- John Deo, CEO at Evercore',
      },
      {
        text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit.',
        author: '--- Jane Smith, CTO at TechCorp',
      },
      {
        text: 'Security is not a product, but a process. It’s about making informed decisions and taking proactive steps.',
        author: '--- Alice Johnson, Security Analyst',
      },
    ],
  };

  // Use defaultInfo if infoP is undefined or has an empty list
  const info =
    infoP && Array.isArray(infoP.list) && infoP.list.length > 0
      ? infoP
      : defaultInfo;

  // Use default icon if none is provided
  const icon = infoP?.icon || defaultInfo.icon;

  // Auto-switch quotes at a set interval
  useEffect(() => {
    const interval = setInterval(() => {
      setActiveIndex((prevIndex) => (prevIndex + 1) % info.list.length);
    }, intervalTime);
    return () => clearInterval(interval);
  }, [info.list.length, intervalTime]);

  return (
    <div className={styles.quote_1_pagePart}>
      {/* Icon */}
      <div className={`${styles.icon} icon_cont_type_1`}>{icon}</div>

      {/* Display only the active quote */}
      <div className={styles.text_contents}>
        <blockquote>{info.list[activeIndex].text}</blockquote>
        <div className={styles.name}>{info.list[activeIndex].author}</div>
      </div>

      {/* Generate buttons for each quote */}
      <div className={styles.button_cont}>
        {info.list.map((_, index) => (
          <button
            key={index}
            className={index === activeIndex ? styles.active : ''}
            onClick={() => setActiveIndex(index)}
          ></button>
        ))}
      </div>
    </div>
  );
};

Quote1PagePart.displayName = 'Quote1PagePart';

export default Quote1PagePart;
