/* heading_1-pagePart.tsx */
import React, { ElementType, CSSProperties } from 'react';

/* import styles */
import styles from './heading_1-pagePart.module.scss';

/* Define data type of props */
interface Props {
  infoP?: {
    headingTag?: string; // Small subheading or label above the main heading
    headingType?: ElementType; // Allows React component elements like h1, h2, etc.
    headingText?: string; // Main heading text content
  };
  stylesP?: {
    [key: string]: CSSProperties; // Custom inline styles
  };
}

/**
 * This component supports passing additional styles via props and displays a default label if none is provided.
 *
 * @param infoP - Pass heading details here
 * @param stylesP - Optional prop to apply custom inline styles to the component.
 * @example
 * ```tsx
 * <Heading1PagePart
 *   infoP={{ headingTag: "Subheading", headingType: "h2", headingText: "Main Title" }}
 *   stylesP={{ heading_1_pagePart: { fontSize: '24px' }, heading_tag: { color: 'blue' }}}
 * />
 * ```
 */

const Heading1PagePart: React.FC<Props> = ({ infoP, stylesP }) => {
  // Default fallback values if no props are provided
  const defaultInfo = {
    headingTag: 'Heading Child',
    headingType: 'h2' as ElementType, // Default heading type
    headingText: 'This is the main heading',
  };

  /* Merge provided infoP with default values */
  const info = infoP || defaultInfo;

  /* Ensure the heading type is a valid JSX element */
  const HeadingTag: ElementType = info.headingType || 'h2';

  return (
    <div
      className={`${styles.heading_1_pagePart} heading_1_pagePart`}
      style={stylesP?.heading_1_pagePart} // Apply stylesP to the outer container
    >
      {/* Render subheading/tag */}
      <div className={styles.heading_tag} style={stylesP?.heading_tag}>
        {info.headingTag}
      </div>

      {/* Render main heading dynamically */}
      <HeadingTag className={styles.heading} style={stylesP?.heading}>
        {info.headingText}
      </HeadingTag>
    </div>
  );
};

Heading1PagePart.displayName = 'Heading1PagePart';

export default Heading1PagePart;
