'use client';

/* pageIntro_2-pagePart.tsx */

import Link from 'next/link';
import { JSX } from 'react';

/* Import custom components */
import Tags1PagePart from '@/components/tags/tags_1-pagePart/tags_1-pagePart';

/* Import media */
import { Circle, AArrowDown } from 'lucide-react';

/* Import styles */
import styles from './pageIntro_2-pagePart.module.scss';

/* Defines data types */
interface PageIntro2PagePartProps {
  tagComp?: JSX.Element; // Custom tag component
  headingType: keyof JSX.IntrinsicElements; // HTML heading tag type
  headingText: string; // Heading text
  list: {
    icon?: JSX.Element; // Accept a component instead of a string
    text: string;
  }[]; // List of items with icons and text
  linkText: string; // Link display text
  linkValue: string; // Link value (unused currently)
  linkHref: string; // URL for the link
}

interface Props {
  infoP?: PageIntro2PagePartProps;
}

/**
 * PageIntro2PagePart Component
 *
 * Displays an introduction section with a heading, list, and call-to-action link.
 *
 * @param infoP props - Component properties
 * @example
 * <PageIntro2PagePart infoP={...} />
 */
const PageIntro2PagePart: React.FC<Props> = ({ infoP }) => {
  // Default information if no props are provided
  const defaultInfo: PageIntro2PagePartProps = {
    tagComp: <Tags1PagePart />,
    headingType: 'h1',
    headingText: 'PentestPilot Vulnerability Management Solution',
    list: [
      {
        icon: <AArrowDown color="green" />, // Store icon as a string key
        text: 'Discover and prioritize active vulnerabilities first',
      },
    ],
    linkText: 'Book a call',
    linkValue: 'book_call',
    linkHref: '#',
  };

  // Use provided props or fall back to default
  const info = infoP || defaultInfo;

  // Dynamic heading tag
  const HeadingTag: keyof JSX.IntrinsicElements = info.headingType;

  return (
    <div className={styles.pageIntro_2_pagePart}>
      {/* Placeholder for tag component */}
      {info.tagComp && (
        <div
          className={`${styles.Tags1PagePart_placeHolder} Tags1PagePart_placeHolder`}
        >
          {info.tagComp}
        </div>
      )}

      {/* Dynamic heading */}
      <HeadingTag className={styles.heading}>{info.headingText}</HeadingTag>

      {/* List section */}
      <div className={styles.list}>
        <ul>
          {info.list.map((item, index) => {
            return (
              <li key={index}>
                {item.icon ? (
                  <div className={styles.icon}>{item.icon}</div>
                ) : (
                  <AArrowDown color="red" />
                )}

                <div className={styles.text}>{item.text}</div>
              </li>
            );
          })}
        </ul>
      </div>

      {/* Call-to-action link */}
      <div className="button_cont_type_1">
        <Link href={info.linkHref} className={styles.link}>
          {info.linkText}
        </Link>
      </div>
    </div>
  );
};

PageIntro2PagePart.displayName = 'PageIntro2PagePart';

export default PageIntro2PagePart;
